/* 3. Device = Tablets, Ipads (portrait) ELEJE*/
@media (min-width: 768px) and (max-width: 1024px) {
  
    .Base_szoveg_egesz_ugy {
        display:flex;
        flex-direction: column; 
        align-items: center;    
        /*border: 3px dashed red;*/
        max-height: 19rem;      
    }   

    .Csomag_cim_ugy {
        display: flex;
        justify-content: center;
        color: rgba(47,218,241,.87);
        color:rgba(40, 164, 241, 0.938);
        color:rgba(40, 164, 241, 0.938);
        font-size: 2rem;
        font-weight: 900;
        padding:2%;            
    }

    .Csomag_cim_ugy h1 {     
         padding-top: 1rem;
         border: 2px solid rgba(47,218,241,.87);
         border:3px solid rgba(40, 164, 241, 0.938);
         border-radius: 3%;
         width:17rem;
         height: 4rem;
         padding-bottom: 3%;
     }   

    .keret_ugy {
        border-radius: 20px;
        border: 1px solid rgba(47,218,241,.87);
        border:1px solid rgba(40, 164, 241, 0.938);
        padding-left:2%;
        padding-right: 2%;
        padding-bottom: 3%;
    }

      .Csomag_ar_ugy {
        display:flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        line-height: 2rem;
        font-size: 2rem;
        font-weight: 900;
        padding-top:3%;
        padding-bottom: 3%;
        padding-left:2%;
        padding-right:2%;
        padding-top: 2rem;
       /* border: 3px solid red;*/
        min-height: 2rem;
        max-height: 2rem;
      /*  min-width: 25rem;
        max-width: 25rem;*/      
    }    

    .Csomag_leiras_ugy {
        max-width: 100%;       
        min-height: 11rem;
        max-height: 11rem;
         /* margin-top: 2%;*/
       /*padding-bottom: 4%;*/
       /* border: 3px solid red;*/       
        padding:0;
        padding-top: 5%;
        margin:0;
    }  

    .Szoveg_ugy {
       /* letter-spacing: -1.5px;
        line-height: 3rem;*/
      /*  width:25rem;*/
        /*border: 3px solid yellow;*/
        text-align: justify; 
        line-height: 2rem;
        font-size: 1.4rem; 
        letter-spacing: -1.2px; 
        padding:0;
        margin:0;
    }   

    .Ered_ugy_ar {
        text-decoration-line: line-through;
    }

   .Csok_ugy_ar {
       color:red;
   }  
}

/* 3. Device = Tablets, Ipads (portrait) VÉGE*/

